body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

.navbar {
  background-color: rgb(28,35,49);
  height: 40px;
  line-height: 40px;
  width: 100%;
  color: white;
  text-align: center;
}

.navbar a {
  display: block;
  color: white;
  text-decoration: none;
}

.navbar a:hover {
  color:gray;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square.highlighted {
  border: 2px solid rgb(204, 0, 0);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
}

.game-board {
  margin-bottom: 20px;
}

.game-info {
  margin-left: 20px;
}
